import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
export default {
  data: () => ({
    months: [
      { name: 'January' },
      { name: 'February' },
      { name: 'March' },
      { name: 'April' },
      { name: 'May' },
      { name: 'June' },
      { name: 'July' },
      { name: 'August' },
      { name: 'September' },
      { name: 'October' },
      { name: 'November' },
      { name: 'December' },
    ],
    no_more_post: 'd-none',
    loadcard: '',
    loading: false,
    content: [],
    page: 1,
    data: [],
    increment: 0,
    disable_next: false,
    disable_color: 'green',
    date: '',
  }),
  async asyncData() {},
  async created() {},
  mounted() {
    this.getposts()
  },
  computed: {
    link(id) {
      return '/world-reminders/' + id
    },
  },
  methods: {
    getposts() {
      this.$axios.$get('/sanctum/csrf-cookie')
      NProgress.start()
      NProgress.inc()

      this.date = new Date().toJSON().slice(0, 10).replace(/-/g, '-')
      if (this.$route.params.id) {
        try {
          this.$axios
            .$get(
              `api/er/page/${this.page}/item/${10}/month/${
                this.$route.params.id
              }`
            )
            .then((res) => {
              if (res.data.length == 0) {
                this.no_more_post = ''
                this.disable_next = true
                this.disable_color = 'grey'
              } else {
                this.disable_next = false
                this.disable_color = 'green'
              }

              if (res.data.length < 10) {
                this.no_more_post = ''
                this.disable_next = true
                this.disable_color = 'grey'
              }

              for (const [key, value] of Object.entries(res.data)) {
                this.increment = this.increment + 1
                this.data.push({
                  id: value.id,
                  slug: value.slug,
                  title: value.title,
                  subtitle: value.subtitle,
                  event_date: value.event_date,
                  country: value.country,
                  content: value.content,
                  created_at: value.created_at,
                  human_date: value.human_date,
                  author: value.author,
                  image: value.image,
                  increment: this.increment,
                  date: value.date,
                  anniversary: value.anniversary,
                })
              }

              this.content = this.data
              NProgress.done()
              this.loadcard = 'd-none'
              this.page = this.page + 1
            })
            .catch((error) => {
              NProgress.done()
              this.loadcard = 'd-none'
            })
            .finally(() => {})
        } catch (error) {
          console.log('error')
        }
      } else {
        try {
          this.$axios
            .$get(`api/er/page/${this.page}/item/${10}/date/${this.date}`)
            .then((res) => {
              if (res.data.length == 0) {
                this.no_more_post = ''
                this.disable_next = true
                this.disable_color = 'grey'
              } else {
                this.disable_next = false
                this.disable_color = 'green'
              }

              if (res.data.length < 10) {
                this.no_more_post = ''
                this.disable_next = true
                this.disable_color = 'grey'
              }

              for (const [key, value] of Object.entries(res.data)) {
                this.increment = this.increment + 1
                this.data.push({
                  id: value.id,
                  slug: value.slug,
                  title: value.title,
                  subtitle: value.subtitle,
                  event_date: value.event_date,
                  country: value.country,
                  content: value.content,
                  created_at: value.created_at,
                  human_date: value.human_date,
                  author: value.author,
                  image: value.image,
                  increment: this.increment,
                  date: value.date,
                  anniversary: value.anniversary,
                })
              }

              this.content = this.data
              NProgress.done()
              this.loadcard = 'd-none'
              this.page = this.page + 1
            })
            .catch((error) => {
              NProgress.done()
              this.loadcard = 'd-none'
            })
            .finally(() => {})
        } catch (error) {
          console.log('error')
        }
      }
    },
  },
}
